/**
 * 单位转换
 */
//接口
import {
    exportResource,
} from "@/api/archives.js";
//压缩图片
import * as imageConversion from 'image-conversion';

const UNITVALUE = {
    '万': 10000,
    '百万': 1000000,
}
//转换数字 如果数字太大，缩减显示方式 例如 2000000000 写成20000万
var numberFmt = function (num, max = 1e8, t = '万') {
    const tep = UNITVALUE[t];
    if (num >= max) {
        return parseInt(num / tep) + '万'
    } else {
        if (num < 100) {
            return Number(Number(num).toFixed(1));
        } else {
            return parseInt(num);
        }
    }
}
/***  下载文件 */
var down = function ({ code, msg }) {
    if (code !== 200) return;
    //下载文件
    const a = document.createElement('a');
    a.setAttribute('download', msg);
    a.setAttribute('href', exportResource + '?delete=true&fileName=' + msg);
    a.click();
}

/**
 * 图片压缩
 * @param {file} 被压缩文件
 * @param {Number} 压缩后大小kb
 * @param {Number} 压缩后宽度px
 * @returns [Promise(file)] 
 */
var compressImg = function (file, size = 1024 * 2, width = 1280) {
    return new Promise((resolve) => {
        let _URL = window.URL || window.webkitURL
        // let isLt2M = file.size / 1024 / 1024 > 2 // 判定图片大小是否小于4MB
        // 这里需要计算出图片的长宽
        let img = new Image()
        img.onload = function () {
            file.width = img.width;
            file.height = img.height;
            let valid = img.width > 1280; // 图片宽度大于1280
            if (valid) {
                imageConversion.compressAccurately(file, {
                    size, //图片大小压缩到1024kb
                    width //宽度压缩到1280px
                }).then(res => {
                    resolve(res);
                })
            } else resolve(file);
        }
        // 需要把图片赋值
        img.src = _URL.createObjectURL(file);
    })
}
/**
 * 判断一个变量不为空
 * @param {any} a 
 * @returns 
 */
var defined = function (a) {
    if (a !== '' && a !== undefined && a !== null && a !== NaN) {
        return true;
    }
    return false;
}

/**
 * 判断一个变量是否是Y yes y YES
 * @param {string} a 
 * @returns 
 */
var isYes = function (a) {
    if (a == 'Y' || a == 'y' || a == 'yes' || a == 'YES') {
        return true;
    }
    return false;
}

/**
 * 自定义校验规则数组
 * @param {}} rule 
 * @param {*} value 
 * @param {*} callback 
 */
var validator_isNumber = (rule, value, callback) => {
    if (value === '') {
        callback(new Error('该项不能为空'));
    } else if (!/^[0-9]\d*$/.test(value)) {
        callback(new Error('请输入合法数字'));
    } else {
        callback();
    }
};


/**
 * 判断是合法经度
 * @param {String | Number} lon 
 * @param {Boolean}  
 */
var isGpsLon = (lon) => {
    return /^[\-\+]?(0(\.\d{1,10})?|([1-9](\d)?)(\.\d{1,10})?|1[0-7]\d{1}(\.\d{1,10})?|180\.0{1,10})$/.test(lon)
};

/**
 * 判断是合法维度
 * @param {String | Number} lon 
 * @param {Boolean}  
 */
var isGpsLat = (lat) => {
    return /^[\-\+]?((0|([1-8]\d?))(\.\d{1,10})?|90(\.0{1,10})?)$/.test(lat)
};





/**
 * 判断国网数据编辑中 是否手动输入起止码值
 * 目前除了四川页岩是导入日明细表，其余公司都不需要导入日明细，只输入起止码
 * @param {string} kcCode 
 * @param {*} callback 
 */
var showStAndEtFunc = (kcCode) => {
    return kcCode != "KC0001";
};





export default {
    numberFmt,
    down,
    compressImg,
    isYes,
    defined,
    validator_isNumber,
    showStAndEtFunc,
    isGpsLon,
    isGpsLat,
}